import * as React from "react"

export default function Testimonials1() {
  return (
    <div className="wrapper">
      <section className="testimonials1">
        <div className="container">
          <div className="testimonials1__item">
            <h5 className="testimonials1__title">My Community Directory</h5>
            <p className="testimonials1__text">
              Is the one place Australians find quality, online health and
              community information to meet these challenges now and in the
              future.
            </p>
          </div>
        </div>
      </section>
    </div>
  );
}

