import * as React from "react";
import Slider from "react-slick";
import sup1 from "../images/support/1.svg";
import sup2 from "../images/support/2.svg";

export default function Support() {
  var settings = {
    infinite: true,
    arrows: false,
  };

  return (
    <section class="support">
      <div class="container">
        <h5 class="support__title">Supported by:</h5>
        <div class="support__items">
          <Slider {...settings}>
            <div class="support__item">
              <img src={sup1} alt="" />
            </div>
            <div class="support__item">
              <img src={sup2} alt="" />
            </div>
            <div class="support__item">
              <img src={sup1} alt="" />
            </div>
            <div class="support__item">
              <img src={sup2} alt="" />
            </div>
            <div class="support__item">
              <img src={sup1} alt="" />
            </div>
          </Slider>
        </div>
      </div>
    </section>
  );
}
