import * as React from "react"
import hero18 from "../../images/hero/hero18.jpg";
import hero19 from "../../images/hero/hero19.jpg";
import hero20 from "../../images/hero/hero20.jpg";

import hero18bg from "../../images/hero/hero18-bg.jpg";
import hero19bg from "../../images/hero/hero19-bg.jpg";
import hero20bg from "../../images/hero/hero20-bg.jpg";

import search from "../../images/icons/search.svg";

export default function HeroMCD() {
  return (
    <div className="wrapper">
      <section className="hero4D">
        <div className="container">
          <div className="hero4D__items">
            <div className="hero4D__box">
              <img className="hero4D__bg" src={hero18} alt="" />
              <img className="hero4D__bg" src={hero19} alt="" />
              <img className="hero4D__bg" src={hero20} alt="" />
            </div>
            <div className="hero4D__mobile-box">
              <img className="hero4D__img" src={hero18bg} alt="" />
              <img className="hero4D__img" src={hero19bg} alt="" />
              <img className="hero4D__img" src={hero20bg} alt="" />
            </div>
            <div className="hero4D__item">
              <div className="hero4D__title-box">
                <h4 className="hero4D__title">
                  Local services, clubs and
                  <br />
                  gropus are <span>a click away</span>
                </h4>
              </div>
              <form className="hero4D__form" action="#">
                <div className="hero4D__input-box">
                  <input
                    className="hero4D__form-input"
                    type="text"
                    placeholder="Where are you?"
                  />
                  <button className="hero4D__box-btn">
                    <img src={search} alt="" />
                  </button>
                </div>
                <button className="hero4D__form-btn">GO</button>
              </form>
              <div className="hero4D__subform">
                We need your location to provide local informaiton
              </div>
              <div className="hero4D__subtitle">
                Welcome to <span>My Community Directory</span>
              </div>
              <p className="hero4D__text">
                Australians face a range of social, health, and welfare
                challenges. Helping each other gives us strength to move
                forward. Together we can chagne the world.
              </p>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}