import * as React from "react";

export default function TestimonialsCustom() {
  return (
    <div class="wrapper">
      <section class="testimonialsCustom">
        <svg
          class="testimonialsCustom__bg"
          width="100%"
          height="232"
          viewBox="0 0 1440 232"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            class="color"
            d="M0 22.1739L1440 0.675781V231.161H0L0 22.1739Z"
            fill="#FFF7F4"
          />
        </svg>
        <div class="container">
          <div class="testimonialsCustom__item">
            <h5 class="testimonialsCustom__title">Custom Tailored Directory</h5>
            <p class="testimonialsCustom__text">
              We can custom design your own version of My Community Directory.
            </p>
          </div>
        </div>
      </section>
    </div>
  );
}
