import * as React from "react";

export default function TextLeft() {
  return (
    <div class="textLeft">
      <div class="container">
        <h5 class="textLeft__title">Australian Coverage</h5>
        <p class="textLeft__text">
          My Community Directory lists organisations that provide services that
          are free or subsidised to the public in thousands of locations across
          Australia. These services are aligned into various Community
          Directories.
        </p>
        <p class="textLeft__text">
          Use the map or the links below to view the various Community
          Directories we currently provide. Alternatively, just use the search
          fields at the top of the page to find what you are looking for.
        </p>
      </div>
    </div>
  );
}
