import React from "react";
import videoBackground from "../images/mcd-aunty.png";

export default function Video() {
  return (
    <div class="video">
      <div class="container">
        <div class="video__content">
          <a
            class="video__link"
            data-fancybox
            href="https://www.youtube.com/watch?v=0ova7d9rQt0"
          >
            <img class="video__img" src={videoBackground} alt="" />
          </a>
        </div>
      </div>
    </div>
  );
}
