import * as React from "react";

export default function TestimonialsMCD() {
  return (
    <div class="wrapper">
      <section class="directory1">
        <div class="directory1__bg-box">
          <svg
            class="directory2__bg"
            width="100%"
            height="406"
            viewBox="0 0 1440 406"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              class="color"
              d="M0 30.3826L1440 0.873047V405.153H0L0 30.3826Z"
              fill="#FFF7F4"
            />
          </svg>
        </div>
        <div class="container">
          <div class="directory1__item">
            <h5 class="directory1__title">What Is My Community Directory</h5>
            <p class="directory1__text">
              My Community Directory is the premier source of connection between
              Government, community organisations, and the public. Whether you
              are looking for the local sports club, or planning the future
              public health needs of an entire region, we are leading the
              development of digital infrastructure to enable better matching,
              planning and provision of health and community services to meet
              individual and emerging community needs.
            </p>
            <div class="directory1__box-bottom">
              <p class="directory1__text-bottom">
                My Community Directory provides the information solution for a
                happier and healthier Australia.
              </p>
              <a
                class="directory1__link"
                href="https://www.mycommunitydirectory.com.au/AboutUs"
              >
                learn more
              </a>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}
